import React, { Component } from 'react';
import './App.css';


class App extends Component {
  render() {
    return (
      <div className="App">
        <br/><br/><br/>
        <Hello/>
      </div>
    );
  }
}

class Hello extends Component {
  displayHello = () => {
    return 'Hello World...!';
  }

  render(){
    const hello = this.displayHello();
    const msg = this.displayMsg('Revoh');

    return(
      <div>
        <h2>{hello}</h2><br/>
        <h3>{msg}</h3>
      </div>
    );
  }
}



export default App;

export const displayMsg = (str) => {
  return str;
}
